import React from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
import { useDispatch } from "react-redux";

import { setUser } from "../../redux/slices/userSlice";
import MainPopup from "../MainPopup/MainPopup";
import ItemInput from "../itemInput/ItemInput";
import PasswordInput from "../PasswordInput/PasswordInput";

import "./popupSignUp.scss";

const PopupSignUp = ({ isOpened, onClose, isNested, handleCloseOnSuccessSignUp }) => {
	const dispatch = useDispatch();

	const [isEmailRegistered, setEmailRegistered] = React.useState(null);
	const emailIsUsedMessage = "We already have a user with such email";

	const handleSignUp = (formikValues, actions) => {
		const { email, password } = formikValues;
		const auth = getAuth();
		createUserWithEmailAndPassword(auth, email, password)
			.then(({ user }) => {
				dispatch(setUser({ email: user.email, token: user.refreshToken, id: user.uid }));
				handleCloseOnSuccessSignUp();
			})
			.catch((error) => {
				if (error.code === "auth/email-already-in-use") {
					setEmailRegistered(email);
					actions.setFieldError("email", emailIsUsedMessage);
				}
			});
	};
	return (
		<MainPopup isOpened={isOpened} onClose={onClose} title="Sign up" type="small" isNested={isNested}>
			<div className="popup-registration__body popup-small__body" tabIndex={-1}>
				<Formik
					initialValues={{ name: "", email: "", phone: "", password: "" }}
					validationSchema={Yup.object({
						name: Yup.string().min(2, "Name is too short").required("Required"),
						email: Yup.string()
							.email("Invalid email address")
							.notOneOf([isEmailRegistered], emailIsUsedMessage)
							.required("Required"),
						phone: Yup.string()
							.matches(
								/^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4,6}$/im,
								"Phone number is not valid"
							)
							.required("*Required"),
						password: Yup.string()
							.matches(/[a-zA-Z]/, "Password can only contain Latin letters.")
							.required("Required")
							.min(8, "Password is too short"),
					})}
					onSubmit={handleSignUp}
				>
					<Form className="popup-registration__form">
						<div className="popup-registration__item">
							<ItemInput inputMode="text" tabIndex={2} name="name" label="Name" placeholder="Julia" />
						</div>
						<div className="popup-registration__item">
							<ItemInput
								inputMode="email"
								tabIndex={2}
								name="email"
								label="Email"
								placeholder="example@gmail.com"
							/>
						</div>
						<div className="popup-registration__item">
							<ItemInput
								inputMode="tel"
								tabIndex={2}
								name="phone"
								label="Phone number"
								placeholder="+1_________"
							/>
						</div>
						<div className="popup-registration__item">
							<PasswordInput tabIndex={2} />
						</div>
						<p className="popup-registration__legacy">By signing up, you agree to the user agreement</p>
						<button className="popup-registration__btn-registration" type="submit" tabIndex={2}>
							Sign up
						</button>
					</Form>
				</Formik>
			</div>
		</MainPopup>
	);
};

export default PopupSignUp;
