import useHttp from './useHttp';

const useGoogleSheetsAPI = (props) => {
    const BASE_URL = process.env.REACT_APP_GOOGLE_SHEETS_BASE_URL;
    const API_KEY = process.env.REACT_APP_GOOGLE_SHEETS_API_KEY;
    const CLIENT_ID = process.env.REACT_APP_GOOGLE_SHEETS_CLIENT_ID;
    const CLIENT_SECRET = process.env.REACT_APP_GOOGLE_SHEETS_CLIENT_SECRET;
    const REFRESH_TOKEN = process.env.REACT_APP_GOOGLE_SHEETS_REFRESH_TOKEN;

    const {
        request,
        loading,
        error,
        setLoading,
        setError
    } = useHttp();

    const _transformBaseData = (productsArr) => {
        const result = [];

        for (let i = 1; i < productsArr.length; i++) {
            const productObject = {
                labels: null,
                price: {
                    old: null,
                    current: null
                },
                discount: null,
                characteristics: [],
                reviews: {
                    count: 0,
                    rating: null,
                    content: null
                }
            };

            productsArr[i].forEach((item, index) => {
                switch (productsArr[0][index]) {
                    case 'productId':
                    case 'title':
                    case 'category':
                        productObject[productsArr[0][index]] = item;
                        break;
                    case 'priceOld':
                        productObject.price.old = item !== '-' ? +item : null;
                        break;
                    case 'priceCurrent':
                        productObject.price.current = item !== '-' ? +item : null;
                        break;
                    case 'discountPercent':
                        if (item !== '-') {
                            if (productObject.discount !== null) {
                                productObject.discount = {
                                    ...productObject.discount,
                                    percent: +item
                                }
                            } else {
                                productObject.discount = {
                                    percent: +item
                                }
                            }
                        }
                        break;
                    case 'discountAmount':
                        if (item !== '-') {
                            if (productObject.discount !== null) {
                                productObject.discount = {
                                    ...productObject.discount,
                                    amount: +item
                                }
                            } else {
                                productObject.discount = {
                                    amount: +item
                                }
                            }
                        }
                        break;
                    case 'labels':
                        productObject[productsArr[0][index]] = (item && item !== '-') ? item.split(',\n') : null;
                        break;
                    case 'description':
                        productObject[productsArr[0][index]] = item && item.split(';\n');
                        break;
                    case 'lightning':
                    case 'engine power,\nW':
                    case 'maximum speed,\nkm/h':
                    case 'weight,\nkg':
                    case 'charging time,\nhours':
                        const title = productsArr[0][index].split(',\n');
                        if (title.length === 2) {
                            productObject["characteristics"].push({
                                "title": title[0].slice(0, 1).toUpperCase() + title[0].slice(1),
                                "value": /\d+/i.test(item) ? +item : item,
                                "unit": title[1]
                            });
                        } else {
                            productObject["characteristics"].push({
                                "title": title[0].slice(0, 1).toUpperCase() + title[0].slice(1),
                                "value": item
                            });
                        }
                        break;
                    case 'reviewsQuantity':
                        productObject.reviews["count"] = +item;
                        break;
                    case 'rating':
                        productObject.reviews["rating"] = item !== '-' ? +item : null;
                        break;
                    default:
                        productObject[productsArr[0][index]] = item !== '-' ? +item : null;
                }
            });
            result.push(productObject);
        }

        return result;
    };

    const _transformImagesData = (imagesArr) => {
        const result = [];

        imagesArr.forEach(arr => {
            const item = [arr[0],
                []
            ];

            for (let i = 1; i < arr.length; i++) {
                const images = arr[i].split(';\n');
                item[1].push({
                    "webp": images[0],
                    "src": images[1]
                })
            }
            result.push(item);
        });

        return result;
    };

    const _transformReviewsData = (reviewsArr) => {
        const result = [];

        reviewsArr.forEach(arr => {
            const item = [arr[0],
                []
            ];

            for (let i = 1; i < arr.length; i++) {
                const reviews = arr[i].split(';\n');
                item[1].push({
                    name: reviews[0],
                    date: reviews[1],
                    title: reviews[2],
                    body: reviews[3],
                    rating: reviews[4]
                })
            }
            result.push(item);
        });

        return result;
    };

    const _combineProductsData = (baseArr, imagesArr, reviewsArr) => {
        baseArr.forEach(product => {
            if (imagesArr && imagesArr.length) {
                imagesArr.forEach(imageArr => {
                    if (product.productId === imageArr[0]) {
                        product.images = imageArr[1];
                    }
                });
            }

            if (reviewsArr && reviewsArr.length) {
                reviewsArr.forEach(reviewArr => {
                    if (product.productId === reviewArr[0] && reviewArr[1].length !== 0) {
                        product.reviews.content = reviewArr[1];
                    }
                });
            }
        });

        return baseArr;
    };

    const _getColumnHeadings = async () => {
        let result;

        if (sessionStorage.getItem('PRODUCTS_COLUMN_HEADINGS')) {
            result = JSON.parse(sessionStorage.getItem('PRODUCTS_COLUMN_HEADINGS'));
        } else {
            const {
                values
            } = await request(`${BASE_URL}/values/Base!A1:Q1/?key=${API_KEY}`);
            result = values;
            sessionStorage.setItem('PRODUCTS_COLUMN_HEADINGS', JSON.stringify(result));
        }

        return result;
    }

    const _getAllProductImages = async () => {
        let result;

        if (sessionStorage.getItem('PRODUCTS_IMAGES')) {
            result = JSON.parse(sessionStorage.getItem('PRODUCTS_IMAGES'));
        } else {
            const {
                values
            } = await request(`${BASE_URL}/values/Images!A1:AD10?majorDimension=COLUMNS&key=${API_KEY}`, true);
            result = values;
            sessionStorage.setItem('PRODUCTS_IMAGES', JSON.stringify(result));
        }

        return result;
    }

    const _getAllProductReviews = async () => {
        let result;

        if (sessionStorage.getItem('PRODUCTS_REVIEWS')) {
            result = JSON.parse(sessionStorage.getItem('PRODUCTS_REVIEWS'));
        } else {
            const {
                values
            } = await request(`${BASE_URL}/values/Reviews!A1:AD30?majorDimension=COLUMNS&key=${API_KEY}`, true);
            result = values;
            sessionStorage.setItem('PRODUCTS_REVIEWS', JSON.stringify(result));
        }

        return result;
    }

    const _getAccessToken = async () => {
        let result;

        const requestNewToken = async () => {
            let result;

            const token = await fetch("https://oauth2.googleapis.com/token", {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify({
                        "client_id": CLIENT_ID,
                        "client_secret": CLIENT_SECRET,
                        "refresh_token": REFRESH_TOKEN,
                        "grant_type": "refresh_token"
                    })
                })
                .then(response => response.json());

            result = {
                access_token: token["access_token"],
                valid_before_timestamp: new Date().getTime() + (3500 * 1000)
            }

            return result;
        }

        if (sessionStorage.getItem('ACCESS_TOKEN')) {
            const token = JSON.parse(sessionStorage.getItem('ACCESS_TOKEN'));

            if (token["valid_before_timestamp"] > new Date().getTime()) {
                result = token;
            } else {
                result = await requestNewToken();
                sessionStorage.setItem('ACCESS_TOKEN', JSON.stringify(result));
            }
        } else {
            result = await requestNewToken();
            sessionStorage.setItem('ACCESS_TOKEN', JSON.stringify(result));
        }

        return result["access_token"];
    }

    const getProducts = async (page, itemsPerPage) => {
        const startRowIndex = typeof (page) === 'number' && typeof (itemsPerPage) === 'number' && page > 1 ? (((page - 1) * itemsPerPage) + 2) : 2;
        const endRowIndex = typeof (page) === 'number' && typeof (itemsPerPage) === 'number' ? startRowIndex + itemsPerPage - 1 : 30;

        const columnHeadings = await _getColumnHeadings();
        const images = await _getAllProductImages();
        const reviews = await _getAllProductReviews();

        const columnValues = await request(`${BASE_URL}/values/Base!A${startRowIndex}:Q${endRowIndex}/?key=${API_KEY}`);


        if (columnValues.values) {
            const base = [...columnHeadings, ...columnValues.values];
            const transformedBase = _transformBaseData(base);
            const transformedImages = _transformImagesData(images);
            const transformedReviews = _transformReviewsData(reviews);
            const result = _combineProductsData(transformedBase, transformedImages, transformedReviews)

            return result;
        }
    };

    const getProduct = async (productId) => {
        const result = await clearSheetFilters().then(async () => {
                if (productId !== 'p0001') {
                    const id = productId.replace(/p0+/i, '');

                    const columnHeadings = await _getColumnHeadings();
                    const images = await _getAllProductImages();
                    const reviews = await _getAllProductReviews();

                    const columnValues = await request(`${BASE_URL}/values/Base!A${id}:Q${id}/?key=${API_KEY}`);

                    if (columnValues.values) {
                        const base = [...columnHeadings, ...columnValues.values];

                        const transformedBase = _transformBaseData(base);
                        const transformedImages = _transformImagesData(images);
                        const transformedReviews = _transformReviewsData(reviews);
                        const result = _combineProductsData(transformedBase, transformedImages, transformedReviews);

                        return result[0];
                    } else {
                        throw new Error(`Product with id "${productId}" does not exist!`);
                    }
                } else {
                    throw new Error(`Product with id "${productId}" does not exist!`);
                }
            })
            .catch(error => {
                setError(true);

            });

        return result;
    }

    const getPagesQuantity = async (itemsPerPage = 1, continueLoading = true) => {
        const data = await request(`${BASE_URL}/values/Base!A2:Q100/?key=${API_KEY}`, continueLoading);
        return Math.ceil(data.values.length / itemsPerPage);
    }

    const getExistingCharacteristics = async () => {
        const base = await request(`${BASE_URL}/values/Base!A1:Q30/?key=${API_KEY}`, true);
        const transformedBase = _transformBaseData(base.values);

        let titles = new Set();

        const characteristics = transformedBase.reduce((acc, item) => (
            [
                ...acc,
                ...item.characteristics,
                {
                    title: 'Price',
                    value: item.price.current,
                    type: 'range',
                    unit: '$'
                }
            ]
        ), []);

        characteristics.forEach(item => titles.add(item.title));
        titles = Array.from(titles);

        const sortedCharacteristics = titles.map(title => {
            const result = {
                title: title,
                values: new Set(),
                itemsCount: {}
            };
            characteristics.forEach(item => {
                if (title === item.title) {

                    if (/\d+/i.test(item.value)) {
                        result.values.add(Number(item.value));
                        result.type = 'range'
                        result.unit = item.unit
                    } else {
                        result.values.add(item.value);
                        result.type = 'no-range'
                    }

                    if (result.itemsCount.hasOwnProperty(item.value)) {
                        result.itemsCount[item.value] += 1;
                    } else {
                        result.itemsCount[item.value] = 1;
                    }
                }
            })
            return {
                ...result,
                values: Array.from(result.values).sort((a, b) => a - b)
            };
        });

        return sortedCharacteristics;
    }

    // method filters and sorts the sheet directly!
    const applySheetFilters = async (checkedFiltersArray, sortingCondition) => {
        const URL = `${BASE_URL}:batchUpdate`;
        const accessToken = await _getAccessToken();

        setLoading(true);

        const filterSpecs = [];
        checkedFiltersArray.forEach(filter => {
            if (filter.checkedItems.length > 0) {
                const numberBetweenFilterCriteriaTemplate = {
                    "condition": {
                        "type": "NUMBER_BETWEEN",
                        "values": [{
                                "userEnteredValue": `${Math.min(...filter.checkedItems)}`
                            },
                            {
                                "userEnteredValue": `${Math.max(...filter.checkedItems)}`
                            }
                        ]
                    }
                };

                switch (filter.title) {
                    case 'Price':
                        filterSpecs.push({
                            "filterCriteria": numberBetweenFilterCriteriaTemplate,
                            "columnIndex": 5
                        })
                        break;
                    case 'Engine power':
                        filterSpecs.push({
                            "filterCriteria": numberBetweenFilterCriteriaTemplate,
                            "columnIndex": 11
                        })
                        break;
                    case 'Maximum speed':
                        filterSpecs.push({
                            "filterCriteria": numberBetweenFilterCriteriaTemplate,
                            "columnIndex": 12
                        })
                        break;
                    case 'Weight':
                        filterSpecs.push({
                            "filterCriteria": numberBetweenFilterCriteriaTemplate,
                            "columnIndex": 13
                        })
                        break;
                    case 'Lightning':
                        filter.checkedItems.length === 1 && filterSpecs.push({
                            "filterCriteria": {
                                "condition": {
                                    "type": "TEXT_EQ",
                                    "values": [{
                                        "userEnteredValue": filter.checkedItems[0]
                                    }]
                                }
                            },
                            "columnIndex": 10
                        })
                        break;
                    default:
                        break;
                }
            }
        });

        const sortSpecs = [];

        switch (sortingCondition) {
            case 'By rating decrease':
                sortSpecs.push({
                    'dimensionIndex': 16,
                    "sortOrder": "DESCENDING"
                })
                break;
            case 'By price increase':
                sortSpecs.push({
                    'dimensionIndex': 5,
                    "sortOrder": "ASCENDING"
                })
                break;
            case 'By price decrease':
                sortSpecs.push({
                    'dimensionIndex': 5,
                    "sortOrder": "DESCENDING"
                })
                break;

            default:
                sortSpecs.push({
                    'dimensionIndex': 0,
                    "sortOrder": "ASCENDING"
                })
                break;
        }

        const request = await fetch(URL, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${accessToken}`
                },
                body: JSON.stringify({
                    "requests": [{
                        "setBasicFilter": {
                            "filter": {
                                "range": {
                                    "sheetId": 0,
                                    "startRowIndex": 0,
                                    "endRowIndex": 100,
                                    "startColumnIndex": 0,
                                    "endColumnIndex": 17
                                },
                                "sortSpecs": sortSpecs,
                                "filterSpecs": filterSpecs
                            }
                        }
                    }],
                    "includeSpreadsheetInResponse": true
                })
            })
            .then(res => {
                if (res.ok) {
                    return res.json();
                }
                throw new Error(`${res.status}`);
            })
            .catch(error => {
                setError(true);
            })
            .finally(() => setLoading(false));

        return request;
    }
    // method clears applied filters and sorting rule!
    const clearSheetFilters = async (sortingCondition) => {
        const URL = `${BASE_URL}:batchUpdate`;
        const accessToken = await _getAccessToken();

        const sortSpecs = [];

        switch (sortingCondition) {
            case 'By rating decrease':
                sortSpecs.push({
                    'dimensionIndex': 16,
                    "sortOrder": "DESCENDING"
                })
                break;
            case 'By price increase':
                sortSpecs.push({
                    'dimensionIndex': 5,
                    "sortOrder": "ASCENDING"
                })
                break;
            case 'By price decrease':
                sortSpecs.push({
                    'dimensionIndex': 5,
                    "sortOrder": "DESCENDING"
                })
                break;

            default:
                sortSpecs.push({
                    'dimensionIndex': 0,
                    "sortOrder": "ASCENDING"
                })
                break;
        }

        const request = await fetch(URL, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${accessToken}`
                },
                body: JSON.stringify({
                    "requests": [{
                            "clearBasicFilter": {
                                "sheetId": 0
                            }
                        },
                        {
                            "setBasicFilter": {
                                "filter": {
                                    "range": {
                                        "sheetId": 0,
                                        "startRowIndex": 0,
                                        "endRowIndex": 100,
                                        "startColumnIndex": 0,
                                        "endColumnIndex": 17
                                    },
                                    "sortSpecs": sortSpecs
                                }
                            }
                        }
                    ],
                    "includeSpreadsheetInResponse": true
                })
            })
            .then(res => res.json());

        return request;
    }

    const getFilteredData = async (checkedFiltersArray, selectedSortOption) => {
        const columnHeadings = await _getColumnHeadings();
        const images = await _getAllProductImages();
        const reviews = await _getAllProductReviews();

        let filteredData = await applySheetFilters(checkedFiltersArray, selectedSortOption)
            .then(async () => {
                const visibleValues = [];
                const columnValues = await request(`${BASE_URL}?ranges=Base!A2:Q100&includeGridData=true&key=${API_KEY}`);

                clearSheetFilters(selectedSortOption);

                columnValues.sheets[0].data[0].rowMetadata.forEach((item, i) => {
                    if (!item.hiddenByFilter && columnValues.sheets[0].data[0].rowData[i].values[0].formattedValue) {
                        visibleValues.push(columnValues.sheets[0].data[0].rowData[i].values.map(value => (value.formattedValue)));
                    }
                });

                if (visibleValues.length) {
                    const base = [...columnHeadings, ...visibleValues];
                    const transformedBase = _transformBaseData(base);
                    const transformedImages = _transformImagesData(images);
                    const transformedReviews = _transformReviewsData(reviews);
                    const result = _combineProductsData(transformedBase, transformedImages, transformedReviews);

                    return result;
                } else {
                    throw new Error('Any product has been found in according to selected filters!')
                }
            })
            .catch(error => {

                return [];
            });

        return filteredData;
    }

    const searchByTitle = async (value) => {
        const URL = `${BASE_URL}:batchUpdate`;
        const accessToken = await _getAccessToken();
        const columnHeadings = await _getColumnHeadings();
        const images = await _getAllProductImages();

        const result = await fetch(URL, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${accessToken}`
                },
                body: JSON.stringify({
                    "requests": [{
                        "setBasicFilter": {
                            "filter": {
                                "range": {
                                    "sheetId": 0,
                                    "startRowIndex": 0,
                                    "endRowIndex": 100,
                                    "startColumnIndex": 0,
                                    "endColumnIndex": 17
                                },
                                "filterSpecs": [{
                                    "filterCriteria": {
                                        "condition": {
                                            "type": "TEXT_CONTAINS",
                                            "values": [{
                                                "userEnteredValue": value
                                            }]
                                        }
                                    },
                                    "columnIndex": 2
                                }]
                            }
                        }
                    }],
                    "includeSpreadsheetInResponse": true
                })
            })
            .then(async () => {
                const visibleValues = [];

                const columnValues = await request(`${BASE_URL}?ranges=Base!A2:Q100&includeGridData=true&key=${API_KEY}`);

                clearSheetFilters();

                columnValues.sheets[0].data[0].rowMetadata.forEach((item, i) => {
                    if (!item.hiddenByFilter && columnValues.sheets[0].data[0].rowData[i].values[0].formattedValue) {
                        visibleValues.push(columnValues.sheets[0].data[0].rowData[i].values.map(value => (value.formattedValue)));
                    }
                })

                if (visibleValues.length) {
                    const base = [...columnHeadings, ...visibleValues];
                    const transformedBase = _transformBaseData(base);
                    const transformedImages = _transformImagesData(images);
                    const result = _combineProductsData(transformedBase, transformedImages);

                    return result;
                }

                return [];
            })
            .catch(error => {

                return [];
            });

        return result;
    }

    return {
        getProduct,
        getProducts,
        getPagesQuantity,
        getExistingCharacteristics,
        applySheetFilters,
        clearSheetFilters,
        getFilteredData,
        searchByTitle,
        loading,
        error,
        setLoading,
        setError
    };
}

export default useGoogleSheetsAPI;