import {
    createSlice
} from '@reduxjs/toolkit';

const productsStatusSlice = createSlice({
    name: 'productsStatus',
    initialState: {
        favorites: {
            items: [],
            quantity: 0
        },
        inCompare: {
            items: [],
            quantity: 0
        },
    },
    reducers: {
        addToFavorites(state, action) {
            state.favorites.items.push(action.payload.productId);
            state.favorites.quantity = state.favorites.items.length;
        },
        removeFromFavorites(state, action) {
            state.favorites.items = state.favorites.items.filter(productId => productId !== action.payload.productId);
            state.favorites.quantity = state.favorites.items.length;
        },
        addToCompare(state, action) {
            state.inCompare.items.push(action.payload.productId);
            state.inCompare.quantity = state.inCompare.items.length;
        },
        removeFromCompare(state, action) {
            state.inCompare.items = state.inCompare.items.filter(productId => productId !== action.payload.productId);
            state.inCompare.quantity = state.inCompare.items.length;
        },
    }
})

export default productsStatusSlice.reducer;
export const {
    addToFavorites,
    removeFromFavorites,
    addToCompare,
    removeFromCompare
} = productsStatusSlice.actions;