import {
    createSlice
} from '@reduxjs/toolkit';

const orderingSlice = createSlice({
    name: 'ordering',
    initialState: {
        productsInCart: [],
        totalProductsQuantity: 0,
        totalPrice: 0,
        deliveryPrice: 10,
        "obtaining method": {
            stepsStatus: {
                1: "editing",
                2: "hidden",
                3: "hidden",
                4: "hidden"
            },
            availableDeliveryDates: {
                minDate: new Date().toISOString(),
                maxDate: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000).toISOString(),
                minTime: new Date('2022-01-01T08:15').toISOString()
            },
            userFormData: {
                country: null,
                address: '',
                date: new Date().toISOString(),
                time: new Date('2022-01-01T08:15').toISOString(),
                message: ''
            }
        },
        "payment method": {
            methods: ['Cash payment', 'Cashless payment', 'Credit/debit card'],
            selectedMethod: 'Credit/debit card'
        },
        recipient: {
            firstName: '',
            lastName: '',
            phoneNumber: '',
            email: '',
            callback: false
        }
    },
    reducers: {
        addProduct(state, action) {
            state.productsInCart.push(action.payload);
            state.totalProductsQuantity = state.totalProductsQuantity + 1;
            state.totalPrice = state.totalPrice + action.payload.price.current;
        },
        removeProduct(state, action) {
            state.productsInCart = state.productsInCart.filter(product => product.productId !== action.payload.productId);
            state.totalProductsQuantity = state.totalProductsQuantity - 1;
            state.totalPrice = state.productsInCart.reduce((acc, item) => (acc += (item.price.current * (item.itemsInCart ? item.itemsInCart : 1))), 0);
        },
        changeProductQuantity(state, action) {
            state.productsInCart = state.productsInCart.map((product => {
                if (product.productId === action.payload.productId) {
                    return {
                        ...product,
                        itemsInCart: action.payload.itemsInCart
                    }
                }

                return product;
            }))
            state.totalPrice = state.productsInCart.reduce((acc, item) => (acc += (item.price.current * (item.itemsInCart ? item.itemsInCart : 1))), 0);
        },
        setDeliveryData(state, action) {
            state["obtaining method"]["userFormData"] = action.payload;
        },
        changeStepsStatus(state, action) {
            state["obtaining method"]["stepsStatus"] = action.payload;
        },
        setPaymentMethod(state, action) {
            state['payment method']['selectedMethod'] = action.payload
        },
        setRecipientData(state, action) {
            state.recipient = action.payload;
        },
        setSuccessOrder(state) {
            state.productsInCart = [];
            state.totalProductsQuantity = 0;
            state.totalPrice = 0;
            state["obtaining method"]["stepsStatus"] = {
                1: "editing",
                2: "hidden",
                3: "hidden",
                4: "hidden"
            }
        }
    }
});

const {
    actions,
    reducer
} = orderingSlice;

export default reducer;
export const {
    addProduct,
    removeProduct,
    changeProductQuantity,
    setDeliveryData,
    changeStepsStatus,
    setPaymentMethod,
    setRecipientData,
    setSuccessOrder
} = actions;