import React from "react";
import { Field, ErrorMessage } from "formik";

import "./itemInput.scss";

const ItemInput = ({ label, name, placeholder, tabIndex = 0, inputMode = "text" }) => {
	return (
		<div className="item-input">
			<label className="item-input__label">
				<span>{label}</span>
				<Field
					className="item-input__input"
					type="text"
					inputMode={inputMode}
					name={name}
					placeholder={placeholder}
					tabIndex={tabIndex}
				/>
			</label>
			<ErrorMessage className="item-input__error" name={name} component="div" />
		</div>
	);
};

export default ItemInput;
