import React from 'react';
import { CSSTransition } from 'react-transition-group';

import { scrollLockContext } from '../../context';
import Portal from '../Portal/Portal';

import './popupOverlay.scss';

const PopupOverlay = ({children, onClose, isOpened, isNested}) => {
    const {isScrollLocked, scrollbarWidth} = React.useContext(scrollLockContext);

    const calculatePaddingRight = () => {
        if (window.innerWidth > 450) {
            return {paddingRight: scrollbarWidth + 15}
        } else if (window.innerWidth <= 450 && window.innerWidth > 320) {
            return {paddingRight: scrollbarWidth + 10}
        } else {
            return {paddingRight: scrollbarWidth}
        }
    }
    
    return (
        <CSSTransition
            classNames="popup-backdrop"
            in={isOpened}
            timeout={500}
            unmountOnExit
        >
            <Portal>
                <div
                    className="popup__container"
                    role="dialog"
                    style={isScrollLocked ? calculatePaddingRight() : null}
                >
                    <div
                        className={"popup__overlay" + (isNested ? ' transparent' : '')}
                        role="button"
                        tabIndex={0}
                        onClick={onClose}
                    />
                    <CSSTransition
                        classNames="popup-body"
                        in={isOpened}
                        timeout={500}
                        unmountOnExit
                        appear
                    >
                        {children}
                    </CSSTransition>
                </div>
            </Portal>
        </CSSTransition>
    );
}

export default PopupOverlay;