import React from "react";

import "./itemCheckbox.scss";

const ItemCheckbox = ({ checked, handleChange = () => null, description, name, tabIndex = 0 }) => {
	const [isChecked, setChecked] = React.useState(checked);

	const handleCheckboxChange = (event) => {
		setChecked((state) => !state);
		handleChange(event);
	};
	return (
		<div className="input-checkbox" data-checked={isChecked}>
			<label className="input-checkbox__label">
				<input
					className="input-checkbox__input"
					type="checkbox"
					tabIndex={tabIndex}
					name={name}
					value={isChecked}
					defaultChecked={isChecked}
					onChange={handleCheckboxChange}
				/>
				<span className="input-checkbox__outline" aria-hidden></span>
				<span className="input-checkbox__description">{description}</span>
			</label>
		</div>
	);
};

export default ItemCheckbox;
