import React from "react";
import { CSSTransition, SwitchTransition } from "react-transition-group";

import { useAuth } from "../../hooks";
import UserAuthorized from "../userAuthorized/UserAuthorized";

import "./headerAuth.scss";

const HeaderAuth = ({ isPopupLoginOpened, isScrollLocked, onClickLogin }) => {
	const { isAuth } = useAuth();

	return (
		<SwitchTransition mode="out-in">
			<CSSTransition key={isAuth} classNames="header-auth-transition" timeout={500}>
				<div className="top-header__auth-wrapper">
					{isAuth ? (
						<UserAuthorized />
					) : (
						<button
							className="top-header__login-btn"
							disabled={isPopupLoginOpened || isScrollLocked}
							onClick={onClickLogin}
							data-popup="PopupLogin"
							tabIndex={1}
							aria-label="Sign in"
						>
							Log in
						</button>
					)}
				</div>
			</CSSTransition>
		</SwitchTransition>
	);
};

export default HeaderAuth;
