import React from 'react';
import { TrapFocus } from '@mui/base';

import { scrollLockContext } from '../../context';
import PopupOverlay from '../PopupOverlay/PopupOverlay';
import PopupHeader from '../PopupHeader/PopupHeader';

import './mainPopup.scss';

const MainPopup = ({
    isOpened,
    type,
    title,
    onClose,
    className,
    isPopupSignUpOpened,
    isNested,
    children,
}) => {
    const {toggleScrollLock} = React.useContext(scrollLockContext);
    const [isPopupVisible, setPopupVisible] = React.useState(isOpened);
    // forbidding to click Escape more then once
    const [isEscapeUsed, setEscapeUsed] = React.useState(false);

    React.useEffect(() => {
        window.addEventListener('keydown', onKeyDown);

        return () => window.removeEventListener('keydown', onKeyDown);
    }, []);

    const closePopup = () => {
        if (!isNested) {
            toggleScrollLock();
        }
        setPopupVisible(false);

        setTimeout(() => {
            onClose();
        }, 500);
    }

    const onKeyDown = React.useCallback((event) => {
        if (!isEscapeUsed && event && event.key === 'Escape') {
            setEscapeUsed(true);
            closePopup();
        }
    }, []);

    return (
        <PopupOverlay isOpened={isPopupVisible} onClose={closePopup} isNested={isNested}>
            <div
                className={"popup__body" + (className ? ` ${className}` : '') + (type === "large" ? " popup__body--large" : '')}
            >
                <PopupHeader
                    title={title}
                    type={type}
                    onClose={closePopup}
                />
                <TrapFocus open={isPopupVisible && !isPopupSignUpOpened} disableAutoFocus>
                    {children}
                </TrapFocus>
            </div>
        </PopupOverlay>
    )
}

export default MainPopup;