import React from "react";
import { Link } from "react-router-dom";

import MainPopup from "../MainPopup/MainPopup";
import { useDebounce, useGoogleSheetsAPI } from "../../hooks";
import { scrollLockContext } from "../../context";

import "./popupSearch.scss";

const PopupSearch = ({ isOpened, onClose }) => {
	const { toggleScrollLock } = React.useContext(scrollLockContext);
	const { searchByTitle } = useGoogleSheetsAPI();
	const [products, setProducts] = React.useState([]);
	const [searchValue, setSearchValue] = React.useState("");
	const debounceSearchValue = useDebounce(searchValue, 500);

	const searchProducts = async (value) => {
		if (value && value.length > 2) {
			const result = await searchByTitle(value);

			setProducts(result);
		}
	};

	const closePopup = () => {
		toggleScrollLock();
		onClose();
	};

	React.useEffect(() => {
		searchProducts(debounceSearchValue);
	}, [debounceSearchValue]);

	return (
		<MainPopup isOpened={isOpened} onClose={onClose} title="Search" type="large">
			<div className="popup-search__body" tabIndex={-1}>
				<form className="popup-search__form" onSubmit={(e) => e.preventDefault()}>
					<input
						type="text"
						className="popup-search__input"
						placeholder="Search"
						tabIndex={1}
						value={searchValue}
						onChange={(e) => setSearchValue(e.target.value)}
					/>
					<button className="popup-search__btn" tabIndex={1}>
						<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
							<path
								d="M13 19C8.58172 19 5 15.4183 5 11C5 6.58172 8.58172 3 13 3C17.4183 3 21 6.58172 21 11C21 15.4183 17.4183 19 13 19Z"
								stroke="#838688"
								strokeWidth="2"
								strokeLinecap="round"
								strokeLinejoin="round"
							/>
							<path
								d="M2.99998 21L7.34998 16.65"
								stroke="#838688"
								strokeWidth="2"
								strokeLinecap="round"
								strokeLinejoin="round"
							/>
						</svg>
					</button>
				</form>
				<div className="popup-search__results">
					{products && products.length && searchValue.length > 2 ? (
						<div className="search-results">
							<ul className="search-results__list">
								{products.map(({ productId, images, title, price }) => (
									<li className="search-results__item" key={productId}>
										<Link
											className="search-result"
											to={`/products/${productId}`}
											onClick={closePopup}
											tabIndex={1}
										>
											<img className="search-result__image" src={images[0]["src"]} alt={title} />
											<p className="search-result__title">{title}</p>
											<p className="search-result__price">$ {price.current}</p>
										</Link>
									</li>
								))}
							</ul>
						</div>
					) : null}
					{products && products.length === 0 && searchValue.length > 2 && (
						<p>Any product has been found. Try to change the search request</p>
					)}
				</div>
			</div>
		</MainPopup>
	);
};

export default PopupSearch;
