import React from 'react';
import { Link } from 'react-router-dom';

import { scrollLockContext } from '../../context';
import MainPopup from '../MainPopup/MainPopup';

import './popupCatalogue.scss';
import gyro from '../../assets/images/icons/catalogue/gyro.svg';
import monocycle from '../../assets/images/icons/catalogue/monocycle.svg';
import electroscooter from '../../assets/images/icons/catalogue/electroscooter.svg';
import seagway from '../../assets/images/icons/catalogue/seagway.svg';
import electroscooters from '../../assets/images/icons/catalogue/electroscooters.svg';
import electrobikes from '../../assets/images/icons/catalogue/electrobikes.svg';
import electroskates from '../../assets/images/icons/catalogue/electroskates.svg';
import electrocars from '../../assets/images/icons/catalogue/electrocars.svg';
import accessories from '../../assets/images/icons/catalogue/accessories.svg';
import smarttoys from '../../assets/images/icons/catalogue/smarttoys.svg';
import smartwatch from '../../assets/images/icons/catalogue/smartwatch.svg';

const PopupCatalogue = ({isOpened, onClose}) => {
    const {toggleScrollLock} = React.useContext(scrollLockContext);

    const closePopup = () => {
        toggleScrollLock();
        onClose();
    }

    return (
        <MainPopup
            isOpened={isOpened}
            onClose={onClose}
            title="Catalogue"
            type="large"
        >
            <nav className="popup-catalogue__body" tabIndex={-1}>
                <ul className="popup-catalogue__list">
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link" tabIndex={1} onClick={closePopup}>
                            <img aria-hidden="true" src={gyro} alt="product icon" className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Gyroscooters</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={electroscooter} alt="product icon"
                                className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Electric scooters</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={monocycle} alt="product icon" className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Monocycles</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={seagway} alt="product icon" className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Seagways</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={electroscooters} alt="product icon"
                                className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Electroscooters</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={electrobikes} alt="product icon"
                                className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Electrobikes</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={electroskates} alt="product icon"
                                className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Electroskates</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={electrocars} alt="product icon"
                                className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Electrocars</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={accessories} alt="product icon"
                                className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Accessories</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={smarttoys} alt="product icon" className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Smart toys</p>
                        </Link>
                    </li>
                    <li className="popup-catalogue__item">
                        <Link to="/catalogue" className="popup-catalogue__item-link disabled" tabIndex={-1}>
                            <img aria-hidden="true" src={smartwatch} alt="product icon"
                                className="popup-catalogue__item-img"/>
                            <p className="popup-catalogue__item-name">Smart Watches</p>
                        </Link>
                    </li>
                </ul>
            </nav>
        </MainPopup>
    );
}

export default PopupCatalogue;