import React from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import { Link } from "react-router-dom";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { useDispatch } from "react-redux";

import { setUser } from "../../redux/slices/userSlice";
import { scrollLockContext } from "../../context";
import MainPopup from "../MainPopup/MainPopup";
import PasswordInput from "../PasswordInput/PasswordInput";
import ItemInput from "../itemInput/ItemInput";
import ItemCheckbox from "../itemCheckbox/ItemCheckbox";

import "./popupLogin.scss";

const PopupLogin = ({ isOpened, onClose, isPopupSignUpOpened, showSignUpPopup, isNested }) => {
	const dispatch = useDispatch();
	const { toggleScrollLock } = React.useContext(scrollLockContext);

	const closePopup = () => {
		toggleScrollLock();
		onClose();
	};

	const handleSignIn = (formikValues, actions) => {
		const { email, password } = formikValues;
		const auth = getAuth();
		signInWithEmailAndPassword(auth, email, password)
			.then(({ user }) => {
				dispatch(setUser({ email: user.email, token: user.refreshToken, id: user.uid }));
				closePopup();
			})
			.catch((error) => {
				if (error.code === "auth/wrong-password") {
					actions.setFieldError("password", "Password is wrong");
				}
				if (error.code === "auth/user-not-found") {
					actions.setFieldError("email", "An account with the email doesn't exist");
				}
			});
	};

	return (
		<MainPopup
			isOpened={isOpened}
			onClose={onClose}
			title="Login"
			type="small"
			isNested={isNested}
			isPopupSignUpOpened={isPopupSignUpOpened}
		>
			<div className="popup-login__body popup-small__body" tabIndex={-1}>
				<Formik
					initialValues={{ email: "", password: "" }}
					validationSchema={Yup.object({
						email: Yup.string().email("Invalid email address").required("Required"),
						password: Yup.string()
							.matches(/[a-zA-Z]/, "Password can only contain Latin letters.")
							.required("Required")
							.min(8, "Password is too short"),
					})}
					onSubmit={handleSignIn}
				>
					<Form className="popup-login__form">
						<div className="popup-login__item">
							<ItemInput inputMode="email" tabIndex={1} name="email" label="Email" />
						</div>
						<div className="popup-login__item">
							<PasswordInput tabIndex={1} />
						</div>
						<Link className="popup-login__forgot-password" to="/anypage" tabIndex={1} onClick={closePopup}>
							Forgot password?
						</Link>
						<div className="popup-login__item popup-login__item--checkbox">
							<ItemCheckbox checked={false} description="Remember me" name="rememberMe" tabIndex={1} />
						</div>
						<button className="popup-login__btn-login" type="submit" tabIndex={1}>
							Log in
						</button>
						<button
							className="popup-login__btn-registration"
							onClick={showSignUpPopup}
							tabIndex={1}
							type="button"
						>
							Sign up
						</button>
					</Form>
				</Formik>
			</div>
		</MainPopup>
	);
};

export default PopupLogin;
