import React from 'react';
import { Link } from 'react-router-dom';
import { TrapFocus } from '@mui/base';

import './BottomHeaderCatalogue.scss';
import gyro from '../../assets/images/icons/catalogue/gyro.svg';
import monocycle from '../../assets/images/icons/catalogue/monocycle.svg';
import electroscooter from '../../assets/images/icons/catalogue/electroscooter.svg';
import seagway from '../../assets/images/icons/catalogue/seagway.svg';
import electroscooters from '../../assets/images/icons/catalogue/electroscooters.svg';
import electrobikes from '../../assets/images/icons/catalogue/electrobikes.svg';
import electroskates from '../../assets/images/icons/catalogue/electroskates.svg';
import electrocars from '../../assets/images/icons/catalogue/electrocars.svg';
import accessories from '../../assets/images/icons/catalogue/accessories.svg';
import smarttoys from '../../assets/images/icons/catalogue/smarttoys.svg';
import smartwatch from '../../assets/images/icons/catalogue/smartwatch.svg';

const BottomHeaderCatalogue = (props) => {
    const {isCatalogueOpened, toggleCatalogue} = props;

        return (
            <TrapFocus open={isCatalogueOpened} disableRestoreFocus>
                <div className={`bottom-header__catalogue${isCatalogueOpened ? ' opened' : ''}`} tabIndex={-1}>
                    <button
                        className="bottom-header__catalogue-box"
                        tabIndex={1}
                        onClick={toggleCatalogue}
                    >
                        <div className="bottom-header__catalogue-burger">
                            <span className="bottom-header__burger-line bottom-header__burger-line--1"></span>
                            <span className="bottom-header__burger-line bottom-header__burger-line--2"></span>
                            <span className="bottom-header__burger-line bottom-header__burger-line--3"></span>
                            <span className="bottom-header__burger-point"></span>
                        </div>
                        <p className="bottom-header__catalogue-title">Product catalog</p>
                    </button>
                    <div className="bottom-header__catalogue-body">
                        <ul className="bottom-header__catalogue-list">
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link" onClick={toggleCatalogue} tabIndex={1}>
                                    <img src={gyro} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Gyroscooters</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={electroscooter} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Electric scooters</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={monocycle} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Monocycles</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={seagway} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Seagways</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={electroscooters} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Electroscooters</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={electrobikes} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Electrobikes</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={electroskates} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Electroskates</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={electrocars} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Electrocars</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={accessories} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Accessories</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={smarttoys} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Smarttoys</p>
                                </Link>
                            </li>
                            <li className="bottom-header__catalogue-item">
                                <Link to="/catalogue" className="bottom-header__catalogue-link disabled" tabIndex={-1}>
                                    <img src={smartwatch} alt=" "
                                        className="bottom-header__catalogue-img"/>
                                    <p className="bottom-header__catalogue-name">Smart Watches</p>
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </TrapFocus>
        );
}

export default BottomHeaderCatalogue;