import React from "react";
import { HashRouter, Routes, Route } from "react-router-dom";

import { useScrollLock } from "../../hooks";
import { scrollLockContext, focusContext } from "../../context";
import Header from "../Header/Header";
import ScrollToTop from "../ScrollToTop/ScrollToTop";
import Footer from "../Footer/Footer";
import PopupNavigation from "../PopupNavigation/PopupNavigation";
import PopupLogin from "../PopupLogin/PopupLogin";
import PopupSignUp from "../PopupSignUp/PopupSignUp";
import PopupCatalogue from "../PopupCatalogue/PopupCatalogue";
import PopupSearch from "../PopupSearch/PopupSearch";
import PopupMore from "../PopupMore/PopupMore";
import Backdrop from "../Backdrop/Backdrop";
import Spinner from "../spinner/Spinner";

import "./App.scss";

const HomePage = React.lazy(() => import(/* webpackChunkName: "HomePage" */ "../../pages/HomePage/HomePage"));
const ProductPage = React.lazy(() =>
	import(/* webpackChunkName: "ProductPage" */ "../../pages/ProductPage/ProductPage")
);
const CataloguePage = React.lazy(() =>
	import(/* webpackChunkName: "CataloguePage" */ "../../pages/CataloguePage/CataloguePage")
);
const OrderingPage = React.lazy(() =>
	import(/* webpackChunkName: "OrderingPage" */ "../../pages/OrderingPage/OrderingPage")
);
const AnyPage = React.lazy(() => import(/* webpackChunkName: "AnyPage" */ "../../pages/AnyPage/AnyPage"));
const Page404 = React.lazy(() => import(/* webpackChunkName: "Page404" */ "../../pages/Page404/Page404"));

const App = () => {
	const [isCatalogueOpened, setCatalogueOpened] = React.useState(false);
	const [isPopupLoginOpened, setPopupLoginOpened] = React.useState(false);
	const [isPopupSignUpOpened, setPopupSignUpOpened] = React.useState(false);
	const [isPopupCatalogueOpened, setPopupCatalogueOpened] = React.useState(false);
	const [isPopupSearchOpened, setPopupSearchOpened] = React.useState(false);
	const [isPopupMoreOpened, setPopupMoreOpened] = React.useState(false);
	const [backdrop, setBackdrop] = React.useState(false);
	const { isScrollLocked, toggleScrollLock, lockScroll, unlockScroll, scrollbarWidth } = useScrollLock();
	const linkToHomepageRef = React.useRef();

	// open/close function for a catalogue in header
	const toggleCatalogue = React.useCallback(() => {
		setCatalogueOpened((state) => {
			setBackdrop(!state);
			return !state;
		});
	}, []);

	const onBackdropClick = () => {
		isCatalogueOpened ? toggleCatalogue() : setBackdrop(false);
		toggleScrollLock();
	};

	const showLoginPopup = React.useCallback(() => {
		toggleScrollLock();
		setPopupLoginOpened(true);
	}, [toggleScrollLock]);

	const closeLoginPopup = () => {
		setPopupLoginOpened(false);

		// ,        Escape,   
		setTimeout(() => document.querySelector('[data-popup="PopupLogin"]')?.focus?.(), 1);
	};

	const showSignUpPopup = (event) => {
		event.preventDefault();
		setPopupSignUpOpened(true);
	};

	const closeSignUpPopup = () => {
		setPopupSignUpOpened(false);

		// ,        Escape,   
		setTimeout(() => document.querySelector(".popup-login__btn-registration")?.focus?.(), 1);
	};

	const handlePopupsCloseOnSuccessRegistration = () => {
		closeSignUpPopup();
		closeLoginPopup();
		toggleScrollLock();
	};

	const showCataloguePopup = (event) => {
		event.preventDefault();
		toggleScrollLock();
		setPopupCatalogueOpened(true);
	};

	const closeCataloguePopup = () => {
		setPopupCatalogueOpened(false);

		// ,        Escape,   
		setTimeout(() => document.querySelector(".popup-navigation__link--catalogue")?.focus?.(), 1);
	};

	const showSearchPopup = (event) => {
		event.preventDefault();
		toggleScrollLock();
		setPopupSearchOpened(true);
	};

	const closeSearchPopup = () => {
		setPopupSearchOpened(false);

		// ,        Escape,   
		setTimeout(() => document.querySelector(".popup-navigation__link--search")?.focus?.(), 1);
	};

	const showMorePopup = (event) => {
		event.preventDefault();
		toggleScrollLock();
		setPopupMoreOpened(true);
	};

	const closeMorePopup = () => {
		setPopupMoreOpened(false);

		// ,        Escape,   
		setTimeout(() => document.querySelector(".popup-navigation__link--more")?.focus?.(), 1);
	};

	return (
		<HashRouter>
			<scrollLockContext.Provider
				value={{ isScrollLocked, toggleScrollLock, lockScroll, unlockScroll, scrollbarWidth }}
			>
				<focusContext.Provider value={{ linkToHomepageRef }}>
					<Header
						isCatalogueOpened={isCatalogueOpened}
						toggleCatalogue={toggleCatalogue}
						isPopupLoginOpened={isPopupLoginOpened}
						isScrollLocked={isScrollLocked}
						toggleScrollLock={toggleScrollLock}
						onClickLogin={showLoginPopup}
					/>
					<ScrollToTop linkToHomepageRef={linkToHomepageRef}>
						<React.Suspense fallback={<Spinner />}>
							<Routes>
								<Route path="/" element={<HomePage />} exact />
								<Route path="/products/:productId/*" element={<ProductPage />} />
								<Route path="/catalogue" element={<CataloguePage />} />
								<Route path="/ordering" element={<OrderingPage />} />
								<Route path="/anypage" element={<AnyPage />} />
								<Route path="*" element={<Page404 />} />
							</Routes>
						</React.Suspense>
					</ScrollToTop>
					<Footer />
					<PopupNavigation
						onClickCatalogue={showCataloguePopup}
						onClickSearch={showSearchPopup}
						onClickMore={showMorePopup}
					/>
					{isPopupLoginOpened && (
						<PopupLogin
							isOpened={isPopupLoginOpened}
							onClose={closeLoginPopup}
							isPopupSignUpOpened={isPopupSignUpOpened}
							showSignUpPopup={showSignUpPopup}
						/>
					)}
					{isPopupSignUpOpened && (
						<PopupSignUp
							isOpened={isPopupSignUpOpened}
							onClose={closeSignUpPopup}
							isNested={true}
							handleCloseOnSuccessSignUp={handlePopupsCloseOnSuccessRegistration}
						/>
					)}
					{isPopupCatalogueOpened && (
						<PopupCatalogue isOpened={isPopupCatalogueOpened} onClose={closeCataloguePopup} />
					)}
					{isPopupSearchOpened && <PopupSearch isOpened={isPopupSearchOpened} onClose={closeSearchPopup} />}
					{isPopupMoreOpened && <PopupMore isOpened={isPopupMoreOpened} onClose={closeMorePopup} />}
					<Backdrop transparent={true} visible={backdrop} onClick={onBackdropClick} />
				</focusContext.Provider>
			</scrollLockContext.Provider>
		</HashRouter>
	);
};

export default App;
