import { useState, useEffect } from "react";
import { useLocation } from "react-router";

const ScrollToTop = (props) => {
    const location = useLocation();

    const getProductId = (str) => {
        const regex = new RegExp(`/products/.+/.+`, 'gi');

        if(regex.test(str)) {
            const productId = str.slice(10, str.lastIndexOf('/'));
            return productId;
        } else {
            return null;
        }
    }

    const [productId, setProductId] = useState(getProductId(location.pathname) || null);

    useEffect(() => {
        // excluding paths: /description, /characteristics, /reviews
        const newProductId = getProductId(location.pathname);

        if (productId === null || newProductId === null || (productId !== newProductId)) {
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            });
            setProductId(newProductId);
            props.linkToHomepageRef.current.focus();
            props.linkToHomepageRef.current.blur();
        }
    }, [location]);

    return <>{props.children}</>
};

export default ScrollToTop;